import React from 'react';
import editIcon from '../images/edit.png';
import setIcon from '../images/set.png';
import EditButton from './EditButton';

class TwoLineContainer extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      editMode: false
    }
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(){
    if(this.state.editMode === false){
      this.setState({
        editMode: true
      });
    } else if(this.state.editMode === true){
      this.setState({
        editMode: false
      });
    }
  }

  render(){
    return(
      <div className={this.props.className}>
        <div className='bgGrayDiv'>
          {this.props.title}
          <EditButton type={this.props.hasImg} onClick={this.handleClick}/>
        </div>
        {React.cloneElement(this.props.children, {editMode: this.state.editMode})}
      </div>
    )
  }
}

export default TwoLineContainer;