import Header from '../../components/Header';
import EntireNav from '../../components/EntrieNav';
import TwoLineContainer from '../../components/TwoLineContainer';
import CurrentPortUsageByTime from '../../components/CurrentPortUsageByTime';

const station_all = window.station.all;
const station_port = window.station.port;
const station_isUse = window.station.isUse;
const error_today = window.error.today;
const error_week = window.error.week;
const error_unresolve = window.error.unresovle;

function Monitor(){

  const MainBoard = () => {
    return(
      <div className='mainBoard'>
        <TwoLineContainer className='currentInstall' title='설치 현황'>
          <div className='installDisplay'>
            <InstallDisplayBox number={station_all} text='전체 설치 대 수'/>
            <InstallDisplayBox number={station_port} text='전체 설치 포트'/>
            <InstallDisplayBox number={station_isUse} text='현재 충전 중'/>
          </div>
        </TwoLineContainer>
        <TwoLineContainer className='currentError' title='오류 발생'>
          <div className='errorDisplay'>
            <ErrorDisplayBox number={error_today} text='오늘'/>
            <ErrorDisplayBox number={error_week} text='7일 이내'/>
            <ErrorDisplayBox number={error_unresolve} text='미해결 오류'/>
          </div>
        </TwoLineContainer>
        <TwoLineContainer className='usage' title='시간대 별 이용'>
          <CurrentPortUsageByTime />
        </TwoLineContainer>
      </div>
    )
  }

  const ErrorDisplayBox = (props) => {
    return(
      <div className='errorDisplayBox'>
        <div className='errorNumber'>{props.number}</div>
        <div className='errorText'>{props.text}</div>
      </div>
    )
  }

  const InstallDisplayBox = (props) => {
    return(
      <div className='installDisplayBox'>
        <div className='installNumber'>{props.number}</div>
        <div className='installText'>{props.text}</div>
      </div>
    )
  }

  return(
    <>
      <Header />
      <EntireNav code="/control" />
      <MainBoard />
    </>
  )
}

export default Monitor;