import React from 'react';
import icon from '../images/human_icon.svg';

const admin_name = window.admin.name;
const admin_last = window.admin.last;

class ConnectUser extends React.Component{
  render(){
    return(
      <div className='userBox'>
        <img src={icon} alt='human_icon'></img>
        <span className='currentUser'>
          {/* 접속자: 우용하(2021 .04 .02 14:24) */}
          접속자: {admin_name}({admin_last})
        </span>
      </div>
    )
  }
}

export default ConnectUser; 