import React from 'react';
import MainNav from './MainNav';
import SubNav from './SubNav';

class EntireNav extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      list: {
        'control': [
          [ '/control', '메인현황판' ],
          [ '/control/station', '스테이션 현황' ],
          [ '/control/charge', '충전 로그' ],
          [ '/control/error', '에러 로그']
        ],
        'station': [
          ['/station', '스테이션 LIST'],
          ['/station/error', '에러 관리']
        ],
        'user': [
          ['/user', '준비중']
        ],
        'app': [
          ['/app', '준비중']
        ],
        'administrator': [
          ['/administrator', '준비중']
        ]
      },
      hoveringCode: props.code
    }
  }
  render(){
    console.log(this.state.hoveringCode);
    return(
      <div className='entireNav' onMouseLeave={this.onMouseLeave}>
        <MainNav
          hoveringCode={this.state.hoveringCode}
          code={this.props.code}
          list={this.state.list}
          onMainNavMouseEnter={this.onMainNavMouseEnter}
        />
        <SubNav defaultCode={this.props.code} code={this.state.hoveringCode} list={this.state.list} />
      </div>
    ); 
  }
  onMainNavMouseEnter = (code) => {
    this.setState({ hoveringCode: code });
    console.log(this.state.hoveringCode);
  };
  onMouseLeave = () => {
    if(this.state.hoveringCode !== this.props.code){
      this.setState({ hoveringCode: this.props.code });
    }
  };
}

export default EntireNav;